import { SDK_API } from '../../constants/endpoints'
import axios from 'axios'

export const getCollectionById = async (consumer, collectionId) => {
  try {
    const res = await axios.get(`${SDK_API}/collection/${collectionId}`, {
      headers: {
        'X-API-KEY': consumer,
      },
    })
    return res.data
  } catch (e) {
    throw new Error(e)
  }
}

