import React, { useState, useEffect } from 'react'
import { utils } from 'ethers'
import { PublicKey } from '@solana/web3.js'
import styled from '@emotion/styled'
import { Box, Flex } from '@chakra-ui/react'
import Copy from '../Svgs/copy'
import CheckCircle from '../Svgs/checkCircle'
import { copyText, truncateAddress } from '../../utils'
import { colors } from '../../styles'
import { BLOCK_EXPLORER, SupportedChainId } from '../../constants/chains'
import { NULL_ADDRESS } from '../../constants'

const Wrapper = styled(Flex)`
  align-items: center;
  position: relative;
`
const Address = styled.a`
  // color: ${({ theme }) => colors.foreground};
  font-size: 14px;
  font-weight: 600;
  margin-right: 5px;
  cursor: pointer;

  &:hover {
    text-decoration: underline !important;
    color: ${({ theme }) => colors.foreground};
  }
`

const Icon = styled(Copy)`
  cursor: pointer;
`
const Copied = styled(CheckCircle)`
  cursor: pointer;
`

const CopyAddress = (props) => {
  const [copied, setCopied] = useState(false)
  const [isValidAddress, setIsValidAddress] = useState(false)
  const { address, addressLength = 5, chainId = 1 } = props

  useEffect(() => {
    try {
      const _isValidAddress = utils.isAddress(address) || PublicKey.isOnCurve(address)
      setIsValidAddress(_isValidAddress)
    } catch (e) {
      console.log(e)
    }
  }, [address])

  const onClickCopy = () => {
    setCopied(true)

    copyText(isValidAddress ? address : NULL_ADDRESS)

    setTimeout(() => {
      setCopied(false)
    }, 1000)
  }

  return (
    <Box {...props}>
      <Wrapper>
        <Address
          href={`${BLOCK_EXPLORER[chainId]}address/${address}${
            chainId === SupportedChainId.SOLANA_DEVNET ? '?cluster=devnet' : ''
          }`}
          target="_blank"
        >
          {isValidAddress ? truncateAddress(address, addressLength) : truncateAddress(NULL_ADDRESS, addressLength)}
        </Address>
        {copied ? (
          <Copied width={24} height={24} iconcolor={copied && 'green'} />
        ) : (
          <Icon width={24} height={24} onClick={onClickCopy} />
        )}
      </Wrapper>
    </Box>
  )
}

export default CopyAddress
