import { CloseIcon } from '@chakra-ui/icons'
import { Box, Button, Flex, Image, Link, Text, useMediaQuery } from '@chakra-ui/react'
import styled from '@emotion/styled'
import { lazy, useCallback, useEffect, useState } from 'react'
import ReadMoreAndLess from 'react-read-more-less'
// import SunEditor from 'suneditor-react'
import CopyAddress from '../../../components/CopyAddress'
import Discord from '../../../components/Svgs/discord'
import TokenIcon from '../../../components/Svgs/tokenIcons'
import Twitter from '../../../components/Svgs/twitter'
import Website from '../../../components/Svgs/website'
import { getCollectionById, getRoadmapById } from '../../../state/collection/source'
import { getImageUrl, makeFriendlyNumber } from '../../../utils'
import {
  Container,
  CoverImage,
  CoverImageWrapper,
  Description,
  DetailContainer,
  DetailsWrapper,
  ProfileImage,
  RoadMapContainer,
  StatsWrapper,
  StyledStatsItem,
} from './styles'

const HtmlToReactParser = require('html-to-react').Parser
const htmlToReactParser = new HtmlToReactParser()

const Mint = lazy(() => import('../../Mint'))

const StyledIcon = styled(CloseIcon)`
  position: fixed;
  top: 10px;
  right: 30px;
  color: white;
  z-index: 3;
  cursor: pointer;
  color: white;
`

const DEFAULT_ROADMAP = `<p style="text-align: center;"><strong><br>
</strong></p>
<p style="text-align: center;"><strong><br>
</strong></p>

<p style="text-align: center;"><strong><br>
</strong></p>

<p style="text-align: center;"><strong><br>
</strong></p>

<p style="text-align: center;"><strong><br>
</strong></p>

<p style="text-align: center;"><span style="font-size: 16px;">​<span style="color: rgb(42, 46, 52);">No roadmap to display</span></span><br>
</p>
`

const CollectionDetails = ({ collectionId, accessKey, closeDrawer }) => {
  const [view, setView] = useState('')
  const [mintOpen, setMintOpen] = useState(false)
  const [collection, setCollection] = useState({})

  const getCollectionData = useCallback(async () => {
    try {
      const _data = await getCollectionById(accessKey, collectionId)
      setCollection(_data)
    } catch (error) {
      console.log(error)
    }
  }, [accessKey, collectionId])

  useEffect(() => {
    getCollectionData()
  }, [])

  const [isDesktop] = useMediaQuery('(min-width: 968px)')

  const getData = useCallback(
    async (id, accessKey) => {
      try {
        const { roadMap } = await getRoadmapById(accessKey, id)
        setView(roadMap)
      } catch (error) {
        console.log(error)
        setView(DEFAULT_ROADMAP)
      }
    },
    [collectionId, accessKey],
  )

  useEffect(() => {
    if (collectionId && accessKey) {
      getData(collectionId, accessKey)
    }
  }, [collectionId, accessKey])

  const onClickMint = () => {
    setMintOpen(true)
  }

  const toggleMint = () => {
    setMintOpen((state) => !state)
  }

  const ReactElement = htmlToReactParser.parse(view)

  return (
    <Container>
      <Mint
        mintOpen={mintOpen}
        onClose={toggleMint}
        collectionId={collectionId}
        accessKey={accessKey}
        collection={collection}
      />
      <StyledIcon onClick={closeDrawer} mt="-36px" mr="-4" w="3.5" h="3.5" />
      <CoverImageWrapper>
        <CoverImage
          backgroundImage={
            collection?.profile?.coverImage &&
            getImageUrl(collection?.profile?.coverImage, { height: 600, quality: 80 })
          }
          h={['150px', '150px', '150px', '250px']}
        />
        <DetailsWrapper
          zIndex={2}
          pos={['relative', 'relative', 'relative', 'absolute', 'absolute']}
          top={['-48px', '-48px', '-48px', '0', '0']}
          p={['0 15px', '0 15px', '0 15px', '120px 40px', '120px 40px']}
        >
          <Flex>
            <Flex alignItems={'center'}>
              <ProfileImage>
                <Image
                  src={
                    collection?.profile?.profileImage &&
                    getImageUrl(collection?.profile?.profileImage, { height: 600, quality: 80 })
                  }
                  height={['96px', '96px', '96px', '120px', '120px']}
                  width={['96px', '96px', '96px', '120px', '120px']}
                  fit="cover"
                  // fallback={<Placeholder />}
                  alt=""
                />
              </ProfileImage>
              {isDesktop && (
                <Box ml="20px" h="120px">
                  <Metadata collection={collection} onClickMint={onClickMint} />
                </Box>
              )}
            </Flex>
            <Flex ml="auto" mt="16" alignItems={'flex-start'}>
              {!isDesktop && <CollectionLinks collection={collection} />}
              {isDesktop && (
                <Button
                  mt="80px"
                  bg="buttonBackground"
                  color="buttonColor"
                  variant="solid"
                  onClick={onClickMint}
                  _hover={{
                    backgroundColor: 'buttonBackground',
                  }}
                >
                  Mint
                </Button>
              )}
            </Flex>
          </Flex>
          {!isDesktop && (
            <Box mt="3">
              <Metadata collection={collection} onClickMint={onClickMint} />
            </Box>
          )}
        </DetailsWrapper>
      </CoverImageWrapper>
      <Stats {...collection?.stats} chainId={collection?.contract?.chainId} />
      <DetailContainer
        px="10"
        alignItems={['center', 'center', 'center', 'initial', 'initial']}
        flexDir={['column', 'column', 'column', 'row', 'row']}
      >
        <RoadMapContainer w="full" h="full" mt={['-10px', '-10px', '-10px', '40px', '40px']} zIndex={0}>
          {ReactElement && ReactElement}
          {/*<SunEditor height="auto" readOnly setAllPlugins={false} disable={true} setContents={view} />*/}
        </RoadMapContainer>
      </DetailContainer>
      {/* <Flex w="98%" justifyContent="flex-end" pb="4">
        <DataSource />
      </Flex> */}
    </Container>
  )
}

const Metadata = ({ collection, onClickMint }) => {
  const [isDesktop] = useMediaQuery('(min-width: 968px)')
  return (
    <>
      <Flex>
        <Text color={['foreground']} textAlign="center" fontSize={'lg'} fontWeight="600">
          {collection?.profile?.name}
        </Text>
      </Flex>
      <Flex alignItems="center" my="2">
        <CopyAddress address={collection?.contract?.contractAddress} chainId={collection?.contract?.chainId} />
        {isDesktop && <CollectionLinks collection={collection} />}
      </Flex>
      <Description color={['foreground']} maxW={['initial', 'initial', 'initial', '80%']}>
        {collection && collection?.profile?.bio && (
          <ReadMoreAndLess className="read-more-content" charLimit={100} readMoreText="  more" readLessText="  less">
            {collection?.profile?.bio}
          </ReadMoreAndLess>
        )}
      </Description>
      {!isDesktop && (
        <Button
          w={['full']}
          bg="buttonBackground"
          color="buttonColor"
          size="lg"
          mt="4"
          variant="solid"
          onClick={onClickMint}
          _hover={{
            backgroundColor: 'buttonBackground',
          }}
        >
          Mint
        </Button>
      )}
    </>
  )
}

const CollectionLinks = ({ collection }) => {
  if (!collection) return null
  return (
    <Flex alignItems={'center'}>
      {collection?.profile?.socialmedia?.website && (
        <Link w="fit-content" ml="12px" href={collection?.profile?.socialmedia?.website} target="_blank">
          <Website width="25px" height="25px" color={'foreground'} />
        </Link>
      )}
      {collection?.profile?.socialmedia?.discord && (
        <Link w="fit-content" ml="12px" href={collection?.profile?.socialmedia?.discord} target="_blank">
          <Discord width="25px" height="25px" color={'foreground'} />
        </Link>
      )}
      {collection?.profile?.socialmedia?.twitter && (
        <Link w="fit-content" ml="12px" href={collection?.profile?.socialmedia?.twitter} target="_blank">
          <Twitter width="25px" height="25px" color={'foreground'} />
        </Link>
      )}
    </Flex>
  )
}

const Stats = ({ chainId, maxSupply, totalSupply, mintPercentage, price }) => {
  return (
    <StatsWrapper>
      <StatsItem
        title="Price"
        value={price?.value}
        Icon={
          <Box mr="1">
            <TokenIcon width={16} height={16} chainId={chainId} />
          </Box>
        }
      />
      <StatsItem title="Items" value={totalSupply} />
      <StatsItem title="Supply" value={maxSupply} />
      <StatsItem title="Mint %" value={mintPercentage} />
    </StatsWrapper>
  )
}

const StatsItem = ({ title, value, Icon }) => {
  return (
    <StyledStatsItem>
      <Text color="foreground" fontSize="14px" fontWeight="400" mr={'5px'}>
        {title}:
      </Text>
      <Flex alignItems="center">
        {Icon && Icon}
        <Text color="foreground" fontSize="14px" fontWeight="600" title={value}>
          {isNaN(value) ? value : makeFriendlyNumber(value)}
        </Text>
      </Flex>
    </StyledStatsItem>
  )
}
export default CollectionDetails
